import DefaultConnector from './connectors/DefaultConnector.js';
import MockConnector from './connectors/MockConnector.js';
import Labels from './helpers/Labels.js';
import Tools from './helpers/Tools.js';
/**
 * @class SDK
 * @typicalname JtiExtension
 */
export default class SDK {
    connector;
    initResponse;
    extensionName;
    captchaId;
    tools;
    constructor() {
        this.tools = new Tools();
    }
    /**
     * Initializes the SDK with the provided options. This should be called before or during startup of the extension in order to retrieve necessary information about the user, language, and more.
     *
     * @async
     * @param {Options.Init} options The initialization options.
     * @returns {Response.Init}
     * @memberof SDK

     */
    async init(options) {
        this.extensionName = options.extensionName;
        this.connector = this.assignConnector(options.mock);
        this.initResponse = await this.connector.init(options);
        this.initResponse.init.labels = Labels.process(options.labels, this.initResponse.init.labels);
        console.log(`🧩 JTI Extension - ${this.extensionName} - init`);
        return this.initResponse;
    }
    /**
     * To be called after each session. Grants reward points and writes the achieved score to the database.
     *
     * @async
     * @param {Options.Result} options The result options.
     * @returns {Response.Result}
     * @memberof SDK
     */
    async setResult(options) {
        if (!this.initResponse)
            throw new Error('Please call init() first');
        if (typeof options.normalizedPoints !== 'number')
            throw new Error('Points must be a number');
        if (typeof options.displayScore !== 'number' && options.displayScore !== null)
            throw new Error('Displayscore must be a number or null');
        const request = { normalizedPoints: options.normalizedPoints, score: options.displayScore };
        return this.connector.setResult(request);
    }
    /**
     * Retrieve the ranking for the provided extensionName and the attached options.
     *
     * @async
     * @param {Options.Ranking} options The ranking options.
     * @returns {Response.Ranking}
     * @memberof SDK
     */
    async getRanking(options) {
        if (!this.initResponse)
            throw new Error('Please call init() first');
        const request = {
            limit: options.limit ?? 10,
            timeRange: options.timeRange ?? 'all-time',
            ascending: options.ascending ?? false,
        };
        return this.connector.getRanking(request);
    }
    /**
     * Helper to set result and fetch ranking in one simple step.
     *
     * @async
     * @param {Options.Result} resultOptions The result options.
     * @param {Options.Ranking} rankingOptions The ranking options.
     * @returns {Response.ResultAndRanking}
     * @memberof SDK
     */
    async setResultAndGetRanking(resultOptions, rankingOptions) {
        if (!this.initResponse)
            throw new Error('Please call init() first');
        const resultRequest = {
            normalizedPoints: resultOptions.normalizedPoints,
            score: resultOptions.displayScore,
        };
        const rankingRequest = {
            limit: rankingOptions?.limit ?? 10,
            timeRange: rankingOptions?.timeRange ?? 'all-time',
            ascending: rankingOptions?.ascending ?? false,
        };
        const { mock, time, result } = await this.connector.setResult(resultRequest);
        const { ranking } = await this.connector.getRanking(rankingRequest);
        return {
            mock,
            time,
            result,
            ranking,
        };
    }
    /**
     * Redirects to the specified `quitUrl`.
     *
     * @memberof SDK
     * @returns {void}
     */
    quit() {
        if (!this.initResponse)
            throw new Error('Please call init() first');
        if (this.initResponse.init.quitUrl && window.top) {
            window.top.location.href = this.initResponse.init.quitUrl;
        }
        else {
            console.log(`🧩 JTI Extension v${process.env.VERSION} - quit`);
        }
    }
    assignConnector(mock) {
        if (mock === true) {
            console.log('MockConnector');
            return new MockConnector(this);
        }
        else {
            return new DefaultConnector(this);
        }
    }
}
