import SDK from './sdk.js';
/**
 * Singleton instance of the SDK.
 *
 * This instance is created and exported as `JtiExtension`. It ensures that
 * only one instance of the `SDK` class exists throughout the application.
 *
 * @singleton
 * @instance JtiExtension
 * @type {SDK}
 */
const JtiExtension = new SDK();
export default JtiExtension;
