export default class Labels {
    static process(defaultLabels, customLabels) {
        if (Object.keys(customLabels).length === 0) {
            console.warn('no custom labels provided');
            return defaultLabels;
        }
        for (let key in defaultLabels) {
            if (!customLabels.hasOwnProperty(key)) {
                console.warn('Custom label for ' + key + ' not found');
                continue;
            }
            defaultLabels[key] = customLabels[key];
        }
        return defaultLabels;
    }
}
