export default class IFrame {
    static check() {
        const { searchParams } = new URL(window.location.href);
        const allowOutsideIFrame = searchParams.get('allowOutsideIframe');
        // if window.top is equal to window.self, then it can't be an iframe
        if (allowOutsideIFrame !== 'true' && window.top === window.self) {
            console.warn('WARN: redirecting to root - extension must be implemented via iframe');
            window.location.href = window.location.origin;
        }
    }
    static getExtensionName() {
        const params = new URLSearchParams(window.location.search);
        const extensionName = params.get('id');
        if (!extensionName) {
            throw new Error('No extension name provided');
        }
        return extensionName;
    }
}
