import CaptchaHelper from './Captcha.js';
export default class Api {
    url;
    captchaId;
    captchaTokenCreator;
    constructor(captchaId) {
        if (captchaId) {
            this.captchaId = captchaId;
            this.captchaTokenCreator = CaptchaHelper.setup(captchaId);
        }
    }
    async get(pathname, params) {
        const finalUrl = this.createFetchUrl(pathname);
        if (params) {
            Object.entries(params).forEach(([key, value]) => {
                // TODO
                if (typeof value === 'object')
                    return finalUrl.searchParams.append(key, value.to.toString());
                finalUrl.searchParams.append(key, value.toString());
            });
        }
        const headers = await this.createHeaders(pathname);
        const config = {
            method: 'GET',
            headers,
        };
        return this.fetchApi(finalUrl, config);
    }
    async post(pathname, body) {
        const finalUrl = this.createFetchUrl(pathname);
        const headers = await this.createHeaders(pathname);
        const config = {
            method: 'POST',
            headers,
            body: JSON.stringify(body),
        };
        return this.fetchApi(finalUrl, config);
    }
    async createHeaders(pathname) {
        const headers = new Headers({
            Accept: 'application/json',
            'Content-Type': 'application/json',
        });
        if (this.captchaId && this.captchaTokenCreator) {
            const action = pathname.replace(/[^A-Za-z/_]/g, '');
            const token = await this.captchaTokenCreator(action);
            headers.set('x-captcha-token', token);
        }
        return headers;
    }
    handleError(response) {
        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }
    }
    createFetchUrl(pathname) {
        const finalUrl = new URL(this.url.href);
        // Remove any trailing slash from the original pathname
        const originalPathname = finalUrl.pathname.replace(/\/$/, '');
        // Remove any leading slash from the new pathname
        const newPathname = pathname.replace(/^\//, '');
        // Combine the two pathnames
        finalUrl.pathname = `${originalPathname}/${newPathname}`;
        return finalUrl;
    }
    async fetchApi(finalUrl, config) {
        const response = await fetch(finalUrl, config);
        this.handleError(response);
        return response.json();
    }
}
