import { mockInitData } from '../mock-data/mockInitData.js';
import { mockRankingData } from '../mock-data/mockRankingData.js';
import { mockResultData } from '../mock-data/mockResultData.js';
import BaseConnector from './BaseConnector.js';
export class MockConnector extends BaseConnector {
    constructor(sdk) {
        super(sdk);
    }
    init(initRequest) {
        return new Promise((resolve, reject) => {
            console.log(`🧩 JTI Extension - init using mock connector`);
            console.log('InitRequest', initRequest);
            const mockData = mockInitData;
            setTimeout(() => {
                this.mockServerResponse(mockData).then(resolve).catch(reject);
            }, 300);
        });
    }
    setResult(request) {
        return new Promise((resolve, reject) => {
            console.log(`🧩 JTI Extension - setResult using mock connector`);
            const mockData = mockResultData;
            setTimeout(() => {
                mockData.result.pointsAwarded = request.normalizedPoints * 122;
                mockData.result.score = request.score;
                this.mockServerResponse(mockData).then(resolve).catch(reject);
            }, 300);
        });
    }
    getRanking(request) {
        return new Promise((resolve, reject) => {
            console.log(`🧩 JTI Extension - getRanking using mock connector`);
            let mockData = request.ascending ? mockRankingData.ascending : mockRankingData.descending;
            setTimeout(() => {
                this.mockServerResponse(mockData).then(resolve).catch(reject);
            }, 300);
        });
    }
    // public trackData(request?: Request.Track): Promise<Response.Track> {
    // 	console.log(`🧩 JTI Extension - getRanking using mock connector`)
    // 	const mockData = mockTrackData
    // 	return this.mockServerResponse<Response.Track>(mockData)
    // }
    // public setState(request: FirstLevelStateData): Promise<StateResponse> {
    // 	console.log(`🧩 JTI Extension - getRanking using mock connector`)
    // 	const state = request
    // 	return this.mockServerResponse<Response.State>({ state })
    // }
    async mockServerResponse(data) {
        const serverResponse = {
            time: new Date().toUTCString(),
            mock: true,
        };
        return { ...serverResponse, ...data };
    }
}
export default MockConnector;
