import path from 'path-browserify';
import Api from '../helpers/Api.js';
import IFrame from '../helpers/IFrame.js';
import BaseConnector from './BaseConnector.js';
export default class DefaultConnector extends BaseConnector {
    params;
    constructor(sdk) {
        super(sdk);
        this.params = new URLSearchParams(window.location.search);
        this.api = new Api(this.params.get('key'));
    }
    async init(request) {
        IFrame.check();
        const extensionId = this.params.get('id');
        if (!extensionId)
            throw new Error('No extension Id provided');
        const baseUrl = this.params.get('url');
        if (!baseUrl)
            throw new Error('No api url extracted');
        this.api.url = new URL(path.join(baseUrl, extensionId));
        const response = await this.api.post('/', request);
        return response;
    }
    async setResult(request) {
        const response = await this.api.post('/result', request);
        console.log(`🧩 JTI Extension - setResult()`);
        return response;
    }
    async getRanking(request) {
        const response = await this.api.get('/ranking', request);
        console.log(`🧩 JTI Extension - getRanking()`);
        return response;
    }
}
